
export const sendSms_cafe24 = (state)=>{
        const userId = 'youngh9110'; // Cafe24  
        const secure = '328ac6ca7b2f71e25d5bc8a3f11d97b7'; // Cafe24  
        const sender = '01033356555'; // 
        const receiver = '010-8237-1502'; // 
        // const message = '';
        const message = `:${state.name}\n:${state.tel}\n:${state.address} ${state.detailAddres}\n:${state.date}\n :${state.object}\n :${state.kind}\n:${state.area}\n :${state.room}\n :${state.bathRoom}\n:${state.veranda}`; //  
  const smsURL = '/sms_sender.php'; // Cafe24 SMS API URL (    )
  const requestData = new FormData();

  requestData.append('user_id', userId);
  requestData.append('secure', secure);
  requestData.append('msg', message);
  requestData.append('rphone', receiver);
  requestData.append('sphone1', sender.substring(0, 3));
  requestData.append('sphone2', sender.substring(3, 7));
  requestData.append('sphone3', sender.substring(7));
  requestData.append('smsType', 'L');
  requestData.append('mode', '1');
  
  fetch(smsURL,{
    method:"post",
    body:requestData
  })


  // axios.post(smsURL,{
  //   ...requestData
  // });


  }
  

